import { v4 as uuidv4 } from "uuid";

export default {
  layout: "admin",
  data() {
    return {
      showForm: false,
      loading: false,
      model: "sales",
      /* rawCriterion */
      rawCriterion: {
        search: {
          key: "name",
          term: ""
        },
        filters: {
          type: "default"
        },
        sortBy: {},
        limit: 20
      },
      list: [],
      sortByFields: ["name", "status"],
      headings: ["_id", "name", "description", "status"]
    };
  },
  async mounted() {
    // await this.fetchList();
  },
  methods: {
    updateList() {
      this.$refs.pagination.fetchResults();
    },
    sortToggled(sortBy) {
      console.log(sortBy);
      this.rawCriterion = {
        ...this.rawCriterion,
        sortBy
      };
    },
    documentFetched(doc) {
      this.showForm = true;
      this.editMode = true;
      this.$refs.updateComponent.populateForm(doc);
    },
    resultsFetched(result) {
      if (result.docs.length === 0) {
        this.list = [];
        return;
      }

      /* extract list */
      this.list = result.docs.map(({ _id, name, description, status }) => {
        return {
          _id,
          name,
          description,
          status
        };
      });
    }
  }
};
