export default {
  props: {
    model: String,
  },
  data() {
    return {
      editMode: false,
      doc: {
        _id: "",
        name: "",
        list: "",
        discountPercentage: 0,
        validityRange: { start: new Date(), end: new Date() },
        description: "",
        status: false,
      },
      allProductLists: [],
      loading: false,
      updated: false,
      errorToast: {
        status: false,
        msg: "",
      },
      
    };
  },
  mounted() {
    this.fetchAllProductLists();
  },
  methods: {
    async fetchAllProductLists() {
      const result = await this.$fetchCollection("product_lists");
      this.allProductLists = result.docs.map(({ _id, name }) => {
        return {
          name,
          value: _id,
        };
      });
      this.allProductLists.unshift({
        name: "Select Product List",
        value: null,
      });
    },
    async updateDocument() {
      this.loading = true;
      const result = await this.$updateDocument(
        this.model,
        this.doc,
        this.editMode
      );
      this.loading = false;

      if (!result.updated) {
        this.errorToast.status = true;
        this.errorToast.msg =
          result.msg !== undefined ? result.msg : "Something went wrong";
        setTimeout(() => (this.errorToast.status = false), 2200);
        return;
      }

      this.$emit("updated");
      this.populateForm(result.doc);
      this.$flash(this);
    },
    async deleteDocument() {
      this.loading = true;
      const result = await this.$deleteDocument(this.model, this.doc._id);
      this.loading = false;

      if (!result.deleted) return;

      this.$emit("updated");
      this.resetForm();
      this.$flash(this);
    },
    populateForm(details) {
      const {
        _id,
        name,
        list,
        discountPercentage,
        validityRange,
        description,
        status,
      } = details;
      this.doc = {
        _id,
        name,
        list,
        validityRange: validityRange
          ? validityRange
          : { start: new Date(), end: new Date() },
        discountPercentage: discountPercentage.toString(),
        description,
        status,
      };
      this.editMode = true;
    },
    closeForm() {
      this.resetForm();
      this.$emit("close");
    },
    resetForm() {
      this.populateForm({
        _id: "",
        name: "",
        list: "",
        discountPercentage: "",
        validityRange: { start: new Date(), end: new Date() },
        description: "",
        status: false,
      });
      this.editMode = false;
    },
  },
};
